import * as Constants from '../constants';

const requestNewsData = (params) => ({
    type: Constants.NEWS_CALL_REQUEST,
    params
});

const getNewsDataSuccess = (data) => ({
    type: Constants.NEWS_CALL_SUCCESS,
    data
});

const getNewsDataFailure = (error) => ({
    type: Constants.NEWS_CALL_FAILURE,
    error
});

export { requestNewsData, getNewsDataSuccess, getNewsDataFailure };