const helpers = {
    parseJsonArrayToUrl: function(object){
        return new URLSearchParams(object).toString();
    },
    splitIdCategory: function (params) {
        const arraySlug = params.split('-');
        const strC = arraySlug[arraySlug.length - 1];
        const arrStrC = strC.split("cg");
        return parseInt(arrStrC[1]);
    },
    splitIdDetail: function (params) {
        const arraySlug = params.split('-');
        return parseInt(arraySlug[arraySlug.length - 1]);
    }
};

export default helpers;