import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import FormSearch from '../FormSearch';
import ProductCategory from '../../ProductCategory/itemProductCategory';
import SlideToggle from "react-slide-toggle";

class Index extends Component {
    showCategory = () => {
        return this.props.data.categoryProduct.map((item,key) => {
            return <ProductCategory data={item} key={key}/>
        })
    }

    render() {
        return (
            <div>
                {/* Hero Section Begin */}
                <section className="hero">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-3">
                                <SlideToggle>
                                    {({ toggle, setCollapsibleElement }) => (
                                        <div className="hero__categories">
                                            <div className="hero__categories__all" onClick={toggle}>
                                                <i className="fa fa-bars" />
                                                <span>Danh mục sản phẩm</span>
                                            </div>
                                            <ul ref={setCollapsibleElement}>
                                                {this.showCategory()}
                                            </ul>
                                        </div>
                                    )}
                                </SlideToggle>
                            </div>
                            <div className="col-lg-9">
                                <div className="hero__search">
                                    <div className="hero__search__form">
                                        <FormSearch/>
                                    </div>
                                    <div className="hero__search__phone">
                                        <div className="hero__search__phone__icon">
                                            <i className="fa fa-phone" />
                                        </div>
                                        <div className="hero__search__phone__text">
                                            <h5>0888.38.17.57</h5>
                                            <span>support 24/7 time</span>
                                        </div>
                                    </div>
                                </div>
                                <div className="hero__item set-bg" data-setbg={this.props.data.banner}>
                                    <div className="hero__text">
                                        <span>Trái cây tươi ngon</span>
                                        <h2>Thực phẩm sạch <br />100% tự nhiên</h2>
                                        <p>Miễn phí vận chuyển và nhận hàng</p>
                                        <Link to="/Product" className="primary-btn">Mua ngay</Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                {/* Hero Section End */}
            </div>
        );
    }
}

export default Index;