import * as Constants from '../constants';

const sendRequestContact = (params) => ({
    type: Constants.CONTACT_SEND_REQUEST,
    params
});

const sendRequestContactSuccess = (data) => ({
    type: Constants.CONTACT_SEND_SUCCESS,
        data
});

const sendRequestContactFailure = (error) => ({
    type: Constants.CONTACT_SEND_FAILURE,
        error
});

export { sendRequestContact, sendRequestContactSuccess, sendRequestContactFailure };