import * as Constants from '../constants';

const requestData = () => ({
    type: Constants.API_CALL_REQUEST
});

const getDataSuccess = (data) => ({
    type: Constants.API_CALL_SUCCESS,
        data
});

const getDataFailure = (error) => ({
    type: Constants.API_CALL_FAILURE,
        error
});

export { requestData, getDataSuccess, getDataFailure };