import { takeEvery, call, put, select, delay } from "redux-saga/effects";
import * as Actions from '../actions';
import {CONTACT_SEND_REQUEST} from '../constants';
import fetchPost from '../services/api/contact';

// watcher saga: watches for actions dispatched to the store, starts worker saga
export function* watcherContactSaga() {
    yield takeEvery(CONTACT_SEND_REQUEST, workerSaga);
}

function* workerSaga() {
    try {
        yield put(Actions.globalShowLoadingAction.globalShowLoading());
        const params = yield select(state => state.payloadContact.params);
        const response = yield call(fetchPost,params);
        const data = response.data;
        // dispatch a success action to the store with the new dog

        yield put(Actions.contactAction.sendRequestContactSuccess(data));
        yield delay(1000);
        yield put(Actions.globalShowLoadingAction.globalHideLoading());
    } catch (error) {
        // dispatch a failure action to the store with the error
        yield put(Actions.contactAction.sendRequestContactFailure(error));
        yield delay(1000);
        yield put(Actions.globalShowLoadingAction.globalHideLoading());
    }
}