import { takeLatest, call, put, select } from "redux-saga/effects";
import * as Actions from '../actions';
import {NEWS_DETAIL_CALL_REQUEST} from '../constants';
import fetchData from '../services/api/news/detail';

// watcher saga: watches for actions dispatched to the store, starts worker saga
export function* watcherNewsDetailSaga() {
    yield takeLatest(NEWS_DETAIL_CALL_REQUEST, workerSaga);
}

function* workerSaga() {
    try {
        const id = yield select(state => state.payloadNewsDetail.id);
        const response = yield call(fetchData,id);
        const data = response.data;
        // dispatch a success action to the store with the new dog
        yield put(Actions.newsDetailAction.getNewsDetailDataSuccess(data));

    } catch (error) {
        // dispatch a failure action to the store with the error
        yield put(Actions.newsDetailAction.getNewsDetailDataFailure(error));
    }
}