import { takeLatest, call, put, select } from "redux-saga/effects";
import * as Actions from '../actions';
import {NEWS_CALL_REQUEST} from '../constants';
import fetchData from '../services/api/news';

// watcher saga: watches for actions dispatched to the store, starts worker saga
export function* watcherNewsSaga() {
    yield takeLatest(NEWS_CALL_REQUEST, workerSaga);
}

function* workerSaga() {
    try {
        const params = yield select(state => state.payloadNews.params);
        const response = yield call(fetchData,params);
        const data = response.data;
        // dispatch a success action to the store with the new dog
        yield put(Actions.newsAction.getNewsDataSuccess(data));

    } catch (error) {
        // dispatch a failure action to the store with the error
        yield put(Actions.newsAction.getNewsDataFailure(error));
    }
}