import { all, fork } from 'redux-saga/effects';
import {watcherDefaultSaga} from './defaultDataSaga';
import {watcherContactSaga} from './contactSaga';
import {watcherHomeSaga} from './homeSaga';
import {watcherIntroductionSaga} from './introductionSaga';
import {watcherProductSaga} from './productSaga';
import {watcherProductDetailSaga} from './productDetailSaga';
import {watcherNewsSaga} from './newsSaga';
import {watcherNewsDetailSaga} from './newsDetailSaga';

export default function* rootSaga() {
    return yield all([
        fork(watcherDefaultSaga),
        fork(watcherContactSaga),
        fork(watcherHomeSaga),
        fork(watcherIntroductionSaga),
        fork(watcherProductSaga),
        fork(watcherProductDetailSaga),
        fork(watcherNewsSaga),
        fork(watcherNewsDetailSaga)
    ]);
}