import * as Constants from '../constants';

const requestProductDetailData = (id) => ({
    type: Constants.PRODUCT_DETAIL_CALL_REQUEST,
    id
});

const getProductDetailDataSuccess = (data) => ({
    type: Constants.PRODUCT_DETAIL_CALL_SUCCESS,
    data
});

const getProductDetailDataFailure = (error) => ({
    type: Constants.PRODUCT_DETAIL_CALL_FAILURE,
    error
});

export { requestProductDetailData, getProductDetailDataSuccess, getProductDetailDataFailure };