import { takeLatest, call, put, select, delay } from "redux-saga/effects";
import * as Actions from '../actions';
import {PRODUCT_CALL_REQUEST} from '../constants';
import fetchData from '../services/api/product';

// watcher saga: watches for actions dispatched to the store, starts worker saga
export function* watcherProductSaga() {
    yield takeLatest(PRODUCT_CALL_REQUEST, workerSaga);
}

function* workerSaga() {
    try {
        const params = yield select(state => state.payloadProduct.params);
        const response = yield call(fetchData,params);
        const data = response.data;
        // dispatch a success action to the store with the new dog
        yield put(Actions.productAction.getProductDataSuccess(data));

    } catch (error) {
        // dispatch a failure action to the store with the error
        yield put(Actions.productAction.getProductDataFailure(error));
    }
}