import { combineReducers } from 'redux';
import showLoading from './showLoading';
import payloadDataDefault from './payloadDataDefault';
import payloadContact from './payloadContact';
import payloadHomeData from './payloadHomeData';
import payloadData from './payloadData';
import payloadProduct from './payloadProduct';
import payloadProductDetail from './payloadProductDetail';
import payloadNews from './payloadNews';
import payloadNewsDetail from './payloadNewsDetail';

const appReducers = combineReducers({
    showLoading,
    payloadDataDefault,
    payloadContact,
    payloadHomeData,
    payloadData,
    payloadProduct,
    payloadProductDetail,
    payloadNews,
    payloadNewsDetail
});

export default appReducers;