import * as Constants from '../constants';

// reducer with initial state
const initialState = {
    fetching: false,
    id: null,
    data: null,
    error: null
};

const payloadProductDetail = (state = initialState, action) => {
    switch (action.type) {
        case Constants.PRODUCT_DETAIL_CALL_REQUEST:
            return { ...state, fetching: true, id: action.id, error: null };
        case Constants.PRODUCT_DETAIL_CALL_SUCCESS:
            return { ...state, fetching: false, data: action.data };
        case Constants.PRODUCT_DETAIL_CALL_FAILURE:
            return { ...state, fetching: false, data: null, error: action.error };
        default:
            return state;
    }
};

export default payloadProductDetail;