import React, {Component} from 'react';
import PageNotFound from '../../components/404';
const loadJs = require('loadjs');

class Index extends Component {
    componentWillMount() {
        loadJs('/js/main.js');
    }
    render() {
        return (
            <div>
                <PageNotFound/>
            </div>
        );
    }
}

export default Index;