import { takeEvery, call, put } from "redux-saga/effects";
import * as Actions from '../actions';
import {HOME_CALL_REQUEST} from '../constants';
import fetchData from '../services/api/home';

// watcher saga: watches for actions dispatched to the store, starts worker saga
export function* watcherHomeSaga() {
    yield takeEvery(HOME_CALL_REQUEST, workerSaga);
}

function* workerSaga() {
    try {
        const response = yield call(fetchData);
        const data = response.data;
        // dispatch a success action to the store with the new dog
        yield put(Actions.homeAction.getHomeDataSuccess(data));

    } catch (error) {
        // dispatch a failure action to the store with the error
        yield put(Actions.homeAction.getHomeDataFailure(error));
    }
}