import React, {Component} from 'react';
import {withRouter} from 'react-router';

class FormSearch extends Component {
    constructor(props) {
        super(props);
        this.state = {
            txtSearch:''
        }
    }

    handleClick = () => {
        console.log(this.state);
        this.props.history.push({
            pathname: '/product/?txtSearch='+this.state.txtSearch,
            state: {results: this.state.txtSearch}
        });
        // if (this.state.txtSearch.length > 0) {
        //     return <Redirect
        //         to={{
        //             pathname: '/product',
        //             state: { results: this.state.txtSearch }
        //         }}
        //     />
        // };
    };

    isChange = (event) => {
        let name = event.target.name;
        let value = event.target.value;
        this.setState({
            [name]:value
        });
    };

    render() {
        return (
            <div>
            <form action="#">
                <input type="text" name="txtSearch" onChange={(e) => this.isChange(e)} placeholder="Nhập sản phẩm cần tìm?" defaultValue={this.state.txtSearch} />
                <button type="button" onClick={() => this.handleClick()} className="site-btn">Tìm Kiếm</button>
            </form>
            </div>
        );
    }
}

export default withRouter(FormSearch);