import React, {Component} from 'react';
import {
    BrowserRouter as Router,
    Link
} from 'react-router-dom';

class Index extends Component {
    render() {
        return (
        <footer className="footer spad">
            <div className="container">
                <div className="row">
                    <div className="col-lg-3 col-md-6 col-sm-6">
                        <div className="footer__about">
                            <div className="footer__about__logo">
                                <Router>
                                    <Link to="/"><img src={this.props.defaultData.logo} alt="Logo" /></Link>
                                </Router>
                            </div>
                            <ul>
                                <li>Address: Trần Xuân Soạn, Quận 7, TP.HCM</li>
                                <li>Phone: 0888.37.17.57</li>
                                <li>Email: minh.quyen784@gmail.com</li>
                            </ul>
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-6 col-sm-6 offset-lg-1">
                        <div className="footer__widget">
                            <h6>Thông tin thêm</h6>
                            <ul>
                                <li><Link to="/introduction">Về chúng thôi</Link></li>
                                <li><Link to="/contact">Liên hệ</Link></li>
                            </ul>
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-12">
                        <div className="footer__widget">
                            <h6>Đăng Ký Nhận Email</h6>
                            <p>Đăng ký để nhận những thông báo mới nhất.</p>
                            <form action="#">
                                <input type="text" name="email" placeholder="Nhập email" />
                                <button type="submit" className="site-btn">Đăng ký</button>
                            </form>
                            <div className="footer__widget__social">
                                <a href="https://facebook.com/tieu.ma.bu.91" target="_blank" rel="noopener noreferrer"><i className="fa fa-facebook" /></a>
                                <a href="https://google.com/" target="_blank" rel="noopener noreferrer"><i className="fa fa-google" /></a>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-12">
                        <div className="footer__copyright">
                            <div className="footer__copyright__text">
                                <p>
                                    © Bản quyền thuộc về <b>Quyền Nguyễn</b>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </footer>
        );
    }
}

export default Index;