import * as Constants from '../constants';

// reducer with initial state
const initialState = {
    fetching: false,
    params: null,
    data: null,
    error: null
};

const payloadContact = (state = initialState, action) => {
    switch (action.type) {
        case Constants.CONTACT_SEND_REQUEST:
            return { ...state, fetching: true, params: action.params,  error: null };
        case Constants.CONTACT_SEND_SUCCESS:
            return { ...state, fetching: false, data: action.data };
        case Constants.CONTACT_SEND_FAILURE:
            return { ...state, fetching: false, data: null, error: action.error };
        default:
            return state;
    }
};

export default payloadContact;