import { takeLatest, call, put, select } from "redux-saga/effects";
import * as Actions from '../actions';
import {PRODUCT_DETAIL_CALL_REQUEST} from '../constants';
import fetchData from '../services/api/product/detail';

// watcher saga: watches for actions dispatched to the store, starts worker saga
export function* watcherProductDetailSaga() {
    yield takeLatest(PRODUCT_DETAIL_CALL_REQUEST, workerSaga);
}

function* workerSaga() {
    try {
        const id = yield select(state => state.payloadProductDetail.id);
        const response = yield call(fetchData,id);
        const data = response.data;
        // dispatch a success action to the store with the new dog
        yield put(Actions.productDetailAction.getProductDetailDataSuccess(data));

    } catch (error) {
        // dispatch a failure action to the store with the error
        yield put(Actions.productDetailAction.getProductDetailDataFailure(error));
    }
}