import { takeLatest, call, put, delay } from "redux-saga/effects";
import * as Actions from '../actions';
import {DEFAULT_CALL_REQUEST} from '../constants';
import fetchData from '../services/api/default';

// watcher saga: watches for actions dispatched to the store, starts worker saga
export function* watcherDefaultSaga() {
    yield takeLatest(DEFAULT_CALL_REQUEST, workerSaga);
}

function* workerSaga() {
    try {
        yield put(Actions.globalShowLoadingAction.globalShowLoading());
        const response = yield call(fetchData);
        const data = response.data;
        // dispatch a success action to the store with the new dog
        yield put(Actions.defaultAction.getDataDefaultSuccess(data));
        yield delay(1000);
        yield put(Actions.globalShowLoadingAction.globalHideLoading());
    } catch (error) {
        // dispatch a failure action to the store with the error
        yield put(Actions.defaultAction.getDataDefaultFailure(error));
        yield delay(1000);
        yield put(Actions.globalShowLoadingAction.globalHideLoading());
    }

}