import * as Constants from '../constants';

const requestProductData = (params) => ({
    type: Constants.PRODUCT_CALL_REQUEST,
    params
});

const getProductDataSuccess = (data) => ({
    type: Constants.PRODUCT_CALL_SUCCESS,
    data
});

const getProductDataFailure = (error) => ({
    type: Constants.PRODUCT_CALL_FAILURE,
    error
});

export { requestProductData, getProductDataSuccess, getProductDataFailure };