import React, {Component} from 'react';
import Header from './components/Header';
import BeginHeader from './components/BeginHeader';
import Footer from './components/Footer';
import Router from './Router';
import * as Actions from "./actions";
import {connect} from "react-redux";
import ShowLoading from './containers/GlobalLoading';
import LoadingFirst from './components/Loading/loadingFirst';

class App extends Component {
    componentDidMount() {
        this.props.fetchPost();
    }

    renderApp = () => {
        if (this.props.fetching === false && this.props.data != null) {
            return (
                <div>
                    <BeginHeader defaultData={this.props.data}/>
                    <Header defaultData={this.props.data}/>
                    <Router/>
                    <Footer defaultData={this.props.data}/>
                </div>
            )
        } else {
            return <LoadingFirst />;
        }
    }

    render() {
        return (
            <div>
                <ShowLoading/>
                {this.renderApp()}
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        fetching: state.payloadDataDefault.fetching,
        data: state.payloadDataDefault.data,
        error: state.payloadDataDefault.error
    };
};

const mapDispatchToProps = dispatch => {
    return {
        fetchPost: () => dispatch(Actions.defaultAction.requestDataDefault())
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
