import React, {Component} from 'react';
import {
    Link,
    NavLink
} from 'react-router-dom';

class Index extends Component {
    render() {
        return (
            <div>
                {/* Page Preloder */}
                {/*<div id="preloder">*/}
                    {/*<div className="loader" />*/}
                {/*</div>*/}
                {/* Humberger Begin */}
                <div className="humberger__menu__overlay" />
                <div className="humberger__menu__wrapper">
                    <div className="humberger__menu__logo">
                        <NavLink to="/" activeClassName="active" exact><img src={this.props.defaultData.logo} alt="Logo" /></NavLink>
                    </div>
                    {/*<div className="humberger__menu__cart">
                        <ul>
                            <li><a href="!#"><i className="fa fa-heart" /> <span>1</span></a></li>
                            <li><a href="!#"><i className="fa fa-shopping-bag" /> <span>3</span></a></li>
                        </ul>
                        <div className="header__cart__price">item: <span>$150.00</span></div>
                    </div>*/}
                    <div className="humberger__menu__widget">

                    </div>
                    <nav className="humberger__menu__nav mobile-menu">
                        <ul>
                            <li><NavLink to="/" activeClassName="active" exact>Trang chủ</NavLink></li>
                            <li><NavLink to="/introdution" activeClassName="active">Giới thiệu</NavLink></li>
                            <li><NavLink to="/product" activeClassName="active">Sản phẩm</NavLink></li>
                            <li><NavLink to="/news" activeClassName="active">Tin tức</NavLink></li>
                            <li><NavLink to="/contact" activeClassName="active">Liên hệ</NavLink></li>
                        </ul>
                    </nav>
                    <div id="mobile-menu-wrap" />
                    <div className="header__top__right__social">
                        <a href="!#"><i className="fa fa-facebook" /></a>
                        <a href="!#"><i className="fa fa-google" /></a>
                    </div>
                    <div className="humberger__menu__contact">
                        <ul>
                            <li><i className="fa fa-envelope" /> minh.quyen784@gmail.com</li>
                        </ul>
                    </div>
                </div>
                {/* Humberger End */}
                {/* Header Section Begin */}
                <header className="header">
                    <div className="header__top">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-6 col-md-6">
                                    <div className="header__top__left">
                                        <ul>
                                            <li><i className="fa fa-envelope" /> minh.quyen784@gmail.com</li>
                                        </ul>
                                    </div>
                                </div>
                                <div className="col-lg-6 col-md-6">
                                    <div className="header__top__right">
                                        <div className="header__top__right__social">
                                            <a href="!#"><i className="fa fa-facebook" /></a>
                                            <a href="!#"><i className="fa fa-google" /></a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-3">
                                <div className="header__logo">
                                    <Link to="/"><img src={this.props.defaultData.logo} alt="Logo" /></Link>
                                </div>
                            </div>
                            <div className="col-lg-6">
                                <nav className="header__menu">
                                    <ul>
                                        <li><NavLink to="/" activeClassName="active" exact>Trang chủ</NavLink></li>
                                        <li><NavLink to="/introdution" activeClassName="active">Giới thiệu</NavLink></li>
                                        <li><NavLink to="/product" activeClassName="active">Sản phẩm</NavLink></li>
                                        <li><NavLink to="/news" activeClassName="active">Tin tức</NavLink></li>
                                        <li><NavLink to="/contact" activeClassName="active">Liên hệ</NavLink></li>
                                    </ul>
                                </nav>
                            </div>
                            {/*<div className="col-lg-3">
                                <div className="header__cart">
                                    <ul>
                                        <li><a href="!#"><i className="fa fa-heart" /> <span>1</span></a></li>
                                        <li><a href="!#"><i className="fa fa-shopping-bag" /> <span>3</span></a></li>
                                    </ul>
                                    <div className="header__cart__price">item: <span>$150.00</span></div>
                                </div>
                            </div>*/}
                        </div>
                        <div className="humberger__open">
                            <i className="fa fa-bars" />
                        </div>
                    </div>
                </header>
                {/* Header Section End */}
            </div>
        );
    }
}

export default Index;