import React, {Component} from 'react';
import {Link} from 'react-router-dom';

class ItemProductCategory extends Component {
    showItem = () => {
        if (this.props.data) {
            return <Link to={'/product/'+this.props.data.name_url+'-cg'+this.props.data.id}>{this.props.data.name}</Link>
        } else {
            return;
        }
    }

    render() {
        return (
            <li>{this.showItem()}</li>
        );
    }
}

export default ItemProductCategory;