import * as Constants from '../constants';

const requestDataDefault = () => ({
    type: Constants.DEFAULT_CALL_REQUEST
});

const getDataDefaultSuccess = (data) => ({
    type: Constants.DEFAULT_CALL_SUCCESS,
    data
});

const getDataDefaultFailure = (error) => ({
    type: Constants.DEFAULT_CALL_FAILURE,
    error
});

export { requestDataDefault, getDataDefaultSuccess, getDataDefaultFailure };