import * as Constants from '../constants';

const requestNewsDetailData = (id) => ({
    type: Constants.NEWS_DETAIL_CALL_REQUEST,
    id
});

const getNewsDetailDataSuccess = (data) => ({
    type: Constants.NEWS_DETAIL_CALL_SUCCESS,
    data
});

const getNewsDetailDataFailure = (error) => ({
    type: Constants.NEWS_DETAIL_CALL_FAILURE,
    error
});

export { requestNewsDetailData, getNewsDetailDataSuccess, getNewsDetailDataFailure };