import React, {Component} from 'react';
import {Route, Switch} from 'react-router-dom';
import NotFound from './containers/404';
import LoadingAll from './components/Loading/loadingAll'

const changeStringController = (string) => {
    return string.charAt(0).toUpperCase() + string.substring(1);
};

const loadContainer = (controller) => (
    //React.lazy(() => import('./containers/' + controller))
    // React.lazy(() => {
    //     return new Promise(resolve => {
    //         setTimeout(() => resolve(import('./containers/' + controller)), 300);
    //     });
    // })
    React.lazy(() => {
        const paramController = changeStringController(controller);
        return Promise.all([
            import('./containers/' + paramController),
            new Promise(resolve => setTimeout(resolve, 300))
        ]).then(([moduleExports]) => moduleExports)
            .catch(err => import('./containers/404/index'));
    })
);

const Spinner = () => {
    return <LoadingAll/>
};

class Router extends Component {

    render() {
        return (
            <Switch>

                <Route path="/:controller/:slug-cg:id" exact={false} component={(data) => {
                    const controller = data.match.params.controller + '/category';
                    const MyContainer = loadContainer(controller);
                    return (
                        <React.Suspense fallback={<Spinner />}>
                            <MyContainer {...data}/>
                        </React.Suspense>
                    );
                }}/>

                <Route path="/:controller/:slug-:id" exact={false} component={(data) => {
                    const controller = data.match.params.controller + '/detail';
                    const MyContainer = loadContainer(controller);
                    return (
                        <React.Suspense fallback={<Spinner />}>
                            <MyContainer {...data}/>
                        </React.Suspense>
                    );
                }}/>

                <Route path="/:controller" exact={false} component={(data) => {
                    const controller = data.match.params.controller + '/index';
                    const MyContainer = loadContainer(controller);
                    return (
                        <React.Suspense fallback={<Spinner />}>
                            <MyContainer {...data}/>
                        </React.Suspense>
                    );
                }}/>

                <Route exact path="/" component={(data) => {
                    const MyContainer = loadContainer('Home');
                    return (
                        <React.Suspense fallback={<Spinner />}>
                            <MyContainer {...data}/>
                        </React.Suspense>
                    );
                }}/>

                <Route path="*" component={NotFound}/>

            </Switch>
        );
    }
}

export default Router;