import * as Constants from '../constants';

const requestHomeData = () => ({
    type: Constants.HOME_CALL_REQUEST
});

const getHomeDataSuccess = (data) => ({
    type: Constants.HOME_CALL_SUCCESS,
        data
});

const getHomeDataFailure = (error) => ({
    type: Constants.HOME_CALL_FAILURE,
        error
});

export { requestHomeData, getHomeDataSuccess, getHomeDataFailure };