import * as Constants from '../constants';

// reducer with initial state
const initialState = {
    fetching: false,
    data: null,
    params: {},
    error: null
};

const payloadProduct = (state = initialState, action) => {
    switch (action.type) {
        case Constants.PRODUCT_CALL_REQUEST:
            return { ...state, fetching: true, error: null, params: action.params };
        case Constants.PRODUCT_CALL_SUCCESS:
            return { ...state, fetching: false, data: action.data };
        case Constants.PRODUCT_CALL_FAILURE:
            return { ...state, fetching: false, data: null, error: action.error };
        default:
            return state;
    }
};

export default payloadProduct;